.inner {
  position: relative;
  width: 500px;
  margin: 0 auto;
  height: 100vh;

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    background: url(../../../public/img/soop.jpg) 50% 20% no-repeat;
    background-size: cover;
  }

  // Responsive adjustment for mobile devices
  @media (max-width: 768px) {
    width: 100%;
  }
}

.listBox {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    z-index: 50;
    left: 0;
    top: 0;
    width: 100%;
    height: 500px;
    position: absolute;
    background: url(../../../public/img/listBox.png) 50% 20% no-repeat;
    background-size: contain;
    @media (max-width: 768px) {
      height: 92vw;

    }
  }
  .listDetailBox {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    padding: 50px 40px;
    height: 400px;
    overflow-y: auto;
    overflow-x: hidden;
    .targetDate {
      text-align: right;
    }
    .btnDiv {
      display: flex;
      padding-bottom: 30px;
      justify-content: center;
      button {
        padding: 3px 5px;
        background-color: #3f3f3f;
        margin: 0 8px;
        color: #fff;
        &:hover {
          background-color: #000;
        }
      }
    }
    .todoDetail {
      font-weight: bold;
      width: 100%;
      word-wrap: break-word; /* 구형 브라우저 호환성을 위해 */
      overflow-wrap: break-word; /* 표준 속성 */
      line-height: 25px;
      margin: 20px 0 10px;
    }
    .backBtn {
      cursor: pointer;
      position: absolute;
      top: 30px;
      left: 20px;
      padding: 3px 5px;
      background-color: #3f3f3f;
      margin: 0 8px;
      color: #fff;

      &:hover {
        background-color: #000;
      }
    }
  }
  ul {
    position: relative;
    padding: 50px 40px;
    z-index: 51;
    overflow-y: auto;
    height: 380px;
    scrollbar-width: none;
    @media (max-width: 768px) {
      padding: 20px 5%;
      height: 81vw;
    }

    li {
      cursor: pointer;
      position: relative;
      display: flex;
      line-height: 50px;
      border-bottom: 1px solid #969696;
      span {
        font-weight: bold;
        @media (max-width: 768px) {
          width: 20%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
      p {
        font-weight: bold;
        &.todoTxt {
          margin: 0 10px;
          width: 70%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
      &:last-child {
        border: none;
      }
    }
  }
}

.pockectList {
  display: flex;
  justify-content: space-between;
  li {
    position: relative;
    z-index: 5;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 33.3333%;
    &:hover {
      transform: scale(1.5);
    }
  }
}
.txtBox {
  position: relative;
  z-index: 5;
  .text {
    width: 100%;
  }
  & > button {
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    padding: 3px 5px;
    // border: 2px solid #000;
    background-color: #3f3f3f;
    color: #fff;
    &:hover {
      background-color: #000;
    }
    @media (max-width: 768px) {
      bottom: 15px;
      font-size: 11px;
    }
  }
  p {
    position: absolute;
    top: 25px;
    left: 7%;
    width: 90%;
    font-weight: 900;
    display: flex;
    align-items: center;
    justify-content: center;
    & > span {
      width: 15%;
    }

    & > input {
      border: 1px solid #969696;
      position: relative;
      top: 2px;
      padding: 3px;
      box-sizing: border-box;
      width: 60%;
      margin: 0 3%;
      &::placeholder {
        color: #555555;
      }
    }
    .react-datepicker-wrapper {
      width: 15%;
      input {
        width: 100%;
      }
    }
  }
}

.status {
  position: relative;
  z-index: 5;
  img {
    margin: 0 auto;
  }
  .name {
    position: absolute;
    top: 107px;
    left: 50%;
    transform: translateX(-50%);
    width: 120px;
    padding: 10px 0;
    text-align: center;
    font-weight: bold;
    background: url(../../../public/img/nameBox.png) 50% 50% no-repeat;
    background-size: contain;
  }
}

.pocketImg {
  img {
    width: 150px;
    @media (max-width: 768px) {
      width: 100%;
      max-width: 150px;
    }
  }
}
.choiceBox {
  position: absolute;
  width: 100%;
  bottom: 0;
}
